header {
  height: 100vh;
  padding-top: 3rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  position: relative;
  height: 100%;
}
/* =================== CTA =================== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* =================== Sociials Icons =================== */
.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

/* =================== ME =================== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  position: absolute;
  width: 15rem;
  height: 30rem;
  left: calc(50% - 7.5rem);
  margin-top: 3rem;
  border-radius: 12rem 12rem 0 0;
  padding: 2rem 1rem 1rem 1rem;
}

/* =================== Scroll Down =================== */
.scroll_down {
    position: absolute;
    right: 0;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* =================== Media Queries (Medium Devices) =================== */
@media screen and (max-width: 1024px) {
    header {
        height: 100vh;
    }
    .me {
      width: 15rem;
      left: calc(50% - 7.5rem);
    }
}
/* =================== Media Queries (Small Devices) =================== */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header_socials, .scroll_down {
        display: none;
    }
}